<template>
	<div class="wrapper">
		<div class="wrapper_bg"></div>
		<div class="form_wrapper">
			<div class="title">根据力智和物魔获取每点物魔换算到面板的物魔</div>
			<el-form :hide-required-asterisk="true" ref="rulesForm" :rules="rules" :model="formData"
				label-width="200px">
				<el-form-item prop="actualAttackPower">
					<template slot="label" class="slot_div">
						物攻 / 魔攻
						<el-tooltip class="item" effect="dark" content="包含武器物攻、强化物攻、公会增益物攻、装备魔法封印物攻、附魔物攻等"
							placement="top-start">
							<i class="el-icon-info icon_style"></i>
						</el-tooltip>
					</template>
					<el-input v-model.number="formData.actualAttackPower"></el-input>
				</el-form-item>
				<el-form-item prop="strengthVal">
					<template slot="label" class="slot_div">
						力量 / 智力
						<el-tooltip class="item" effect="dark" content="打开左上角头像面板中显示的力智(力量或智力取决于你要计算的是物攻还是魔攻)"
							placement="top-start">
							<i class="el-icon-info icon_style"></i>
						</el-tooltip>
					</template>
					<el-input v-model.number="formData.strengthVal"></el-input>
				</el-form-item>
				<el-form-item>
					<template slot="label" class="slot_div">
						是否有百分比词条
						<el-tooltip class="item" effect="dark"
							content="包含复古武器装扮以及装备中的词条,词条为物理攻击力+5% 魔法攻击力+5%,有则选择是并输入,没有选择否" placement="top-start">
							<i class="el-icon-info icon_style"></i>
						</el-tooltip>
					</template>
					<el-radio-group v-model="formData.isPercentage">
						<el-radio label="是"></el-radio>
						<el-radio label="否"></el-radio>
					</el-radio-group>
				</el-form-item>
				<el-form-item v-if="formData.isPercentage=='是'" prop="percentage" label="百分比提升">
					<el-input placeholder="不用输入%符号" v-model.number="formData.percentage"></el-input>
				</el-form-item>
				<div v-else style="height: 62px;">

				</div>
				<div v-if="formData.sumATKNum&&formData.eachPointIncreasesAttack">
					<el-form-item label="面板总物攻">
						<div class="val-style">{{formData.sumATKNum}}</div>
					</el-form-item>
					<el-form-item label="每点物攻提升面板">
						<div class="val-style">{{formData.eachPointIncreasesAttack}}</div>
					</el-form-item>
				</div>
				<div v-else style="height: 124px;">

				</div>
				<div class="button_group">
					<el-form-item label-width="0">
						<el-button type="primary" @click="computedPromote">计算</el-button>
					</el-form-item>
					<el-form-item label-width="0">
						<el-button type="primary" @click="resetForm('rulesForm')">重置</el-button>
					</el-form-item>
				</div>
			</el-form>
		</div>
	</div>
</template>

<script>
	export default {
		name: "dnfmComputed",
		data() {
			return {
				screenHeight: 0,
				rules: {
					strengthVal: [{
							required: true,
							message: "请输入力量/智力"
						}, {
							type: "number",
							message: "请输入数字"
						},
						{
							type: "number",
							max: 1000000000,
							message: "最大不超过10位",
						}
					],
					actualAttackPower: [{
							required: true,
							message: "请输入物攻/魔攻"
						}, {
							type: "number",
							message: "请输入数字"
						},
						{
							type: "number",
							max: 1000000000,
							message: "最大不超过10位",
						},
					],
					percentage: [{
							required: true,
							message: "请输入百分比提升"
						}, {
							type: "number",
							message: "请输入数字"
						},
						{
							type: "number",
							max: 1000000000,
							message: "最大不超过10位",
						},
					]
				},
				formData: {
					isPercentage: "否",
					strengthVal: "",
					actualAttackPower: "",
					percentage: "",
					eachPointIncreasesAttack: "",
					sumATKNum: ""
				},
				resetFormData: {

				}
			}
		},
		mounted() {
			this.resetFormData = JSON.parse(JSON.stringify(this.formData))
			this.screenHeight = window.innerHeight
			window.addEventListener('resize', this.handleResize)
		},
		beforeDestroy() {
			window.removeEventListener('resize', this.handleResize)
		},
		methods: {
			resetForm(formName) {
				this.$refs[formName].resetFields();
				this.formData = JSON.parse(JSON.stringify(this.resetFormData))
			},
			handleResize() {
				this.screenHeight = window.innerHeight
			},
			rulesFormFun() {
				let isBoolean = false
				this.$refs['rulesForm'].validate((valid) => {
					if (valid) {
						isBoolean = true
					} else {
						isBoolean = false
					}
				});
				return isBoolean
			},
			computedPromote() {
				if (!this.rulesFormFun()) {
					return
				}
				let fromData = JSON.parse(JSON.stringify(this.formData))
				let sumATK = 0
				let liftPerPoint = 0
				if (fromData.isPercentage == "否") {
					sumATK = parseFloat(fromData.actualAttackPower) * (1 + parseFloat(fromData.strengthVal) / 250)
					liftPerPoint = sumATK / fromData.actualAttackPower
					console.log("总面板物攻", sumATK)
					console.log("每点装备物攻提升面板物攻", liftPerPoint)
					this.formData.sumATKNum = sumATK
					this.formData.eachPointIncreasesAttack = liftPerPoint
				} else {
					let sumATKAfter = parseFloat(fromData.actualAttackPower) * (1 + parseFloat(fromData.strengthVal) / 250)
					console.log(1 + (fromData.percentage / 100))
					let sumATKBefore = sumATKAfter * (1 + (fromData.percentage / 100))
					liftPerPoint = (sumATKAfter / fromData.actualAttackPower) * (1 + (fromData.percentage / 100))
					console.log("百分比计算前面板总物攻", sumATKAfter)
					console.log("百分比计算后面板总物攻", sumATKBefore)
					console.log("力量不变情况下每点物攻增加面板物攻", liftPerPoint)
					this.formData.sumATKNum = sumATKBefore
					this.formData.eachPointIncreasesAttack = liftPerPoint
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	#kvVideo {
		position: absolute;
		width: 100%;
		min-width: 1600px;
		left: 50%;
		top: 0;
		transform: translate3d(-50%, 0, 0);
	}

	.wrapper {
		position: relative;
		min-width: 1200px;
		height: 1787px;
		position: relative;
		width: 100%;
		box-sizing: border-box;

		.title {
			font-family: simhei;
			text-align: center;
			padding: 40px 10px;
			color: rgb(255, 251, 227);
			font-size: 30px;
			font-weight: bold;
		}

		.wrapper_bg {
			width: 100%;
			height: 100%;
			background-image: url(../image/bg01.jpg);
			background-size: cover;
			background-position: center;
			background-repeat: no-repeat;
			position: absolute;
			top: 0;
			left: 0;
			z-index: -1;
		}

		.form_wrapper {
			position: absolute;
			top: 1100px;
			width: 800px;
			left: 50%;
			transform: translate(-50%, -50%);
		}

		.button_group {
			display: flex;
			justify-content: space-evenly;
		}
	}

	::v-deep {
		.el-form-item__label {
			color: #fff;
			font-size: 18px;
		}

		.icon_style {
			transition: all .3s;
			cursor: pointer;

			&:hover {
				color: #ffff93 !important;
			}
		}

		.el-radio__label {
			color: #fff;
		}

		.el-radio__input.is-checked+.el-radio__label {
			color: #fff;
		}

		.el-radio__input.is-checked .el-radio__inner {
			border: rgb(255, 255, 255);
			background-color: rgb(255, 0, 0);
		}

		.el-form-item__error {
			color: #ffe6e6;
		}

		.el-input__inner {
			color: #fff;
			font-size: 16px;
			background-color: rgb(103, 0, 6);
			border-color: transparent !important;

			&::placeholder {
				color: #ececec;
			}
		}

		.val-style {
			color: #fff;
			font-size: 20px;
		}

		.el-button--primary {
			background-color: rgb(103, 0, 6);
			border-color: rgb(0, 0, 0);
		}

		.el-button {
			color: #fff;

			&:hover {
				background-color: rgb(172, 0, 12);
				border-color: #fffbe3;
				color: #fffbe3;
			}
		}
	}
</style>
